"use client";
import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Sheet, SheetContent, SheetTrigger } from "@/components/ui/sheet";
import { DropdownMenu, DropdownMenuContent, DropdownMenuTrigger, DropdownMenuItem } from "@/components/ui/dropdown-menu";
import { fetchData } from "@/app/_lib/categoryReducer";
import UserMenu from "./UserMenu";
import SearchBar from "./SearchBar";
import MenuBar from "./MenuBar";
import { ShoppingCart, Menu } from "lucide-react";

export default function Header() {
  const dispatch = useDispatch();
  const isLoggedIn = useSelector((state) => state.session.user);
  const categories = useSelector((state) => state.categories);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  useEffect(() => {
    dispatch(fetchData("/products/category"));
  }, [dispatch]);

  return (
    <header className="bg-gray-900 text-white w-full shadow-md">
      <nav className="px-4 lg:px-6 py-3 flex justify-between items-center max-w-screen-xl mx-auto">
        {/* Logo */}
        <Link href="/" className="text-2xl font-bold flex flex-col">
          <span className="text-white">Shop<span className="text-orange-500">Flow</span></span>
          <span className="text-xs font-light text-gray-400">Global leading online shop</span>
        </Link>

        {/* Search Bar - Hidden on Mobile */}
        <div className="hidden md:block w-1/2">
          <SearchBar />
        </div>

        {/* Desktop Menu */}
        <div className="hidden lg:flex items-center space-x-6">
          <UserMenu />
          <Link href="/cart" className="relative">
            <ShoppingCart className="w-6 h-6 text-gray-300 hover:text-white" />
            {isLoggedIn && (
              <span className="absolute top-0 right-0 w-5 h-5 text-xs font-bold text-white bg-orange-500 rounded-full flex items-center justify-center transform translate-x-2 -translate-y-2">
                {/* Cart Count */}
              </span>
            )}
          </Link>
        </div>

        {/* Mobile Menu */}
        <Sheet>
          <SheetTrigger asChild>
            <Button variant="ghost" className="lg:hidden text-gray-300 hover:text-white p-2">
              <Menu className="w-6 h-6" />
            </Button>
          </SheetTrigger>
          <SheetContent side="left" className="bg-gray-800 text-white w-64 p-4">
            <UserMenu />
            <Link href="/cart" className="flex items-center mt-4">
              <ShoppingCart className="w-5 h-5" />
              <p className="ml-3">Cart</p>
            </Link>
            {/* Mobile Category Menu */}
            <div className="mt-6">
              {categories?.data?.data?.map((category) => (
                <DropdownMenu key={category.id}>
                  <DropdownMenuTrigger asChild>
                    <Button variant="ghost" className="w-full text-left text-white uppercase">
                      {category.name}
                    </Button>
                  </DropdownMenuTrigger>
                  {category.subCategories && (
                    <DropdownMenuContent align="start" className="bg-white text-black w-40 shadow-lg">
                      {category.subCategories.map((item) => (
                        <DropdownMenuItem key={item.id}>
                          <Link href={`/products/subcategory/${item.id}`} className="w-full block py-1 px-2 hover:text-gray-600">
                            {item.name}
                          </Link>
                        </DropdownMenuItem>
                      ))}
                    </DropdownMenuContent>
                  )}
                </DropdownMenu>
              ))}
            </div>
          </SheetContent>
        </Sheet>
      </nav>
      
      {/* Search Bar for Mobile */}
      <div className="md:hidden px-4 pb-3">
        <SearchBar />
      </div>
      
      {/* Categories Menu */}
      <MenuBar menuData={categories?.data?.data} />
    </header>
  );
}
